header,
main,
footer {
  padding-left: 240px; }

body {
  backgroud: white;
  height: 100%; }
  body .fixed-header {
    width: 100%;
    height: 100%; }
    body .fixed-header .App {
      width: 100%;
      height: 100%; }

@media only screen and (max-width: 992px) {
  header,
  main,
  footer {
    padding-left: 0; } }

#credits li,
#credits li a {
  color: white; }

#credits li a {
  font-weight: bold; }

.footer-copyright .container,
.footer-copyright .container a {
  color: #BCC2E2; }

.fab-tip {
  position: fixed;
  right: 85px;
  padding: 0px 0.5rem;
  text-align: right;
  background-color: #323232;
  border-radius: 2px;
  color: #FFF;
  width: auto; }

.page-container > .row {
  margin: 0rem; }

.App.menu-pin .page-container {
  padding-left: 0px; }
  .App.menu-pin .page-container .page-content-wrapper .content {
    padding-left: 280px; }
  .App.menu-pin .page-container .page-content-wrapper .footer {
    left: 280px; }

.App.box-layout {
  background-color: #fff; }
  .App.box-layout .page-container .page-content-wrapper .footer {
    width: auto; }

.App .teal {
  background-color: transparent !important; }

.draganddrop {
  background-color: red; }

.page-sidebar {
  transform: translate(0px, 0px);
  width: 280px;
  height: 100%; }
  .page-sidebar .sidebar-header {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap; }
    .page-sidebar .sidebar-header .brand {
      font-family: 'Pacifico', cursive;
      width: 50%;
      height: inherit; }
      .page-sidebar .sidebar-header .brand img {
        width: 90%;
        height: inherit; }
    .page-sidebar .sidebar-header .sidebar-header-controls {
      -webkit-transform: translateX(-10px);
      -ms-transform: translateX(-10px);
      transform: translateX(-10px); }
      .page-sidebar .sidebar-header .sidebar-header-controls button i {
        height: auto; }
  .page-sidebar:hover {
    transform: translate(210px, 0px); }
    .page-sidebar:hover .sidebar-header-controls {
      -webkit-transform: translateX(18px);
      -ms-transform: translateX(18px);
      transform: translateX(18px); }
  .page-sidebar .sidebar-menu .scroll-wrapper {
    position: relative; }
    .page-sidebar .sidebar-menu .scroll-wrapper .menu-items {
      height: auto;
      margin-bottom: 0px;
      margin-right: 0px;
      max-height: 738px; }
      .page-sidebar .sidebar-menu .scroll-wrapper .menu-items .row {
        padding: 0rem;
        margin: 0rem; }
      .page-sidebar .sidebar-menu .scroll-wrapper .menu-items li {
        margin: 0px;
        padding: 0.6rem 0rem; }
        .page-sidebar .sidebar-menu .scroll-wrapper .menu-items li a {
          color: #788195;
          line-height: 28px;
          float: left;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          font-size: 14px; }
          .page-sidebar .sidebar-menu .scroll-wrapper .menu-items li a .row {
            margin: 0rem; }
            .page-sidebar .sidebar-menu .scroll-wrapper .menu-items li a .row > .col {
              margin: 0rem;
              padding: 0rem; }
          .page-sidebar .sidebar-menu .scroll-wrapper .menu-items li a .details {
            line-height: 16px;
            font-size: 12px;
            opacity: .4;
            display: block;
            clear: both; }
        .page-sidebar .sidebar-menu .scroll-wrapper .menu-items li .icon-thumbnail {
          padding: 0px;
          margin: 0px; }
        .page-sidebar .sidebar-menu .scroll-wrapper .menu-items li > :last-child {
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-content: center; }
    .page-sidebar .sidebar-menu .scroll-wrapper .scroll-element .scroll-bar {
      width: 89px; }
    .page-sidebar .sidebar-menu .scroll-wrapper:last-child .scroll-element_outer .scroll-bar {
      height: 647px;
      top: 0px; }

.menu-pin .page-sidebar {
